.materials {
  position: relative;

  &__side-text {
    display: none;
    position: absolute;

    @include onTablet {
      display: block;
      top: 35%;
      z-index: 3;
    }

    @include onDesktop {
      display: none;
    }
  }

  &__text {
    position: relative;

    &-container {
      display: none;
      right: 10%;
      top: 72%;
      width: 340px;
      position: absolute;

      @include onTablet {
        display: block;
        z-index: 1;
      }

      @include onDesktop {
        transform: translateX(-20%) translateY(-30%);
        z-index: 1;
        width: 400px;
      }
    }

    &-title {
      color: $c-list;
      font-family: $main-font;
      font-size: 20px;
      font-weight: 700;
      line-height: 150%;
    }

    &-info {
      color: $c-list;
      font-family: $main-font;
      font-size: 16px;
      font-weight: 400;
      line-height: 150%;

      &--bold {
        font-weight: 600;
      }

      &--hidden {
        @include onTablet {
          opacity: 0;
        }

        @include onTablet {
          opacity: 1;
        }
      }
    }
  }

  &__title {
    position: relative;
    padding-inline: 30px;
    color: $c-turquoise;
    font-family: $text-font;
    font-size: 70px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 9.6px;
    z-index: 5;

    @include onTablet {
      font-size: 80px;
      line-height: 96px;
      max-width: 600px;
      padding-inline: 30px;
      margin-bottom: 140px;
    }

    @include onDesktop {
      font-size: 138px;
      font-style: normal;
      font-weight: 400;
      line-height: 167px;
      letter-spacing: 0.15em;
      max-width: max-content;
      padding: 30 0 0 135px;
      margin: 0;
    }
  }

  &__bg {
    height: 70vw;
    max-height: 1080px;
    background: linear-gradient(180deg, #fff 0%,
   rgba(255, 255, 255, 0) 40.93%),
    url(/images/materials/materials-bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    transform: translateY(-82px);
    z-index: -1;

    &-content {
      position: relative;
    }
  }

  &__slider {
    margin: 0 auto;
    padding: 30px 45px;
    transform: translateY(-130px);
    transition: transform $anim-duration, border-radius $anim-duration;

    @include onTablet {
      position: absolute;
      bottom: 15%;
      left: 5%;
    }

    @include onDesktop {
      &:hover {
        transform: translateY(-120px);
        border-radius: 20%;
      }
    }
  }

  &__tablet-button {
    display: none;
    position: absolute;
    width: 51px;
    height: 51px;
    border: none;
    border-radius: 50%;
    color: #fff;
    background-color: $c-red;
    transition: transform $anim-duration;

    &:hover {
      cursor: pointer;
      transform: scale(125%);
    }

    &--active {
      background: rgba(36, 35, 35, 0.75);
      opacity: 0.65;
      backdrop-filter: blur(12px);
    }

    @include onTablet {
      display: block;
    }

    &--1 {
      bottom: 50%;
      left: 70%;

      @include onDesktop {
        bottom: 30%;
        left: 77%;
      }
    }

    &--2 {
      bottom: 18%;
      left: 58%;

      @include onDesktop {
        bottom: 22%;
        left: 58%;
      }
    }

    &--3 {
      bottom: 8%;
      left: 44%;

      @include onDesktop {
        bottom: 7%;
        left: 47%;
      }
    }

    &--4 {
      bottom: 35%;
      left: 30%;

      @include onDesktop {
        bottom: 35%;
        left: 38%;
      }
    }

    &--5 {
      bottom: 10%;
      left: 10%;

      @include onDesktop {
        bottom: 10%;
        left: 18%;
      }
    }
  }
}
