.side-text {
  color: $c-side-text;
  font-family: $text-font;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0.22em;

  text-transform: uppercase;
  transform: rotate(90deg);

  &::before {
    content: "";
    background-image: url(/images/icons/side-text-line.svg);
    display: block;
    position: relative;
    top: 6px;
    left: -80px;
    width: 65px;
    height: 1px;
  }
}
